<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('LC_NUMBER') }}:</span>
          <span>{{ lc.lcNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LC_TYPE') }}:</span>
          <span>{{ lc.typeName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ADDITIONAL_REF') }}:</span>
          <span>{{ lc.auxiliary1 | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('BENEFICIARY_NAME') }}:</span>
          <span>{{ lc.beneficiaryName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ISSUANCE_DATE') }}:</span>
          <span>{{ lc.issuanceDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('EXPIRY_DATE') }}:</span>
          <span>{{ lc.expiryDate | formatUnixDate }}</span>
        </p>
      </div>
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('CURRENCY') }}:</span>
          <span>{{ lc.currencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LC_AMOUNT') }}:</span>
          <span>{{ lc.lcAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('OUTSTANDING_AMOUNT') }}:</span>
          <span>{{ lc.outstandingAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ADVISING_BANK') }}:</span>
          <span>{{ lc.advisingBank | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MARGIN_CURRENCY') }}:</span>
          <span>{{ lc.marginCurrencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MARGIN_AMOUNT') }}:</span>
          <span>{{ lc.marginAmount | currency('') | placeholder('-') }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import TradeFinanceService from '@/services/trade/TradeFinanceService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'

export default {
  name: 'import-lc-issued-view',
  mixins: [LoadingMixin, AlertMixin, DatePickerMixin],
  data: function () {
    return {
      lc: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      TradeFinanceService.issuedImportLcs({
        lcNumber: this.$route.params.id,
        fromDate: this.getTime(this.$route.params.fromDate),
        toDate: this.getTime(this.$route.params.toDate)
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.lc = response.data.lcList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
